<template>
  <div class="create-order">
    <div class="row">
      <div class="col">
        <div class="create-order__form">
          <form @submit.prevent="createOrder">
            <div class="form-group">
              <label for="orderName">Название:</label>
              <input type="text" class="form-control" id="orderName" v-model="form.name" required>
            </div>
            <div class="form-group">
              <label for="orderPrice">Цена продажи:</label>
              <input type="number" class="form-control" id="orderPrice" v-model="form.price" required>
            </div>
            <div class="form-group">
              <label for="orderAmount">Количество:</label>
              <input type="number" class="form-control" id="orderAmount" v-model="form.amount" required>
            </div>
            <div class="create-order__composition">
              <span
                class="badge rounded-pill bg-secondary"
                v-for="p in selectProducts"
                :key="p.id"
                @click="deleteCompositionItem(p.id)"
              >{{p.name}}({{p.value | unit(p.unit)}})
              </span>
            </div>
            <div class="create-order__cost">
              <b>Себестоимость:</b> {{orderCost}} руб.
            </div>
            <button type="submit" class="btn btn-primary">Добавить</button>
          </form>
        </div>
      </div>
      <div class="col">
        <div class="create-order__products">
          <v-table>
            <thead>
              <tr>
                <th scope="col">Название</th>
                <th scope="col">В наличии</th>
                <th scope="col"></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="p in storage" :key=p.id>
                <td>{{ p.name }}</td>
                <td>{{ p.value | unit(p.unit) }}</td>
                <td>
                  <button type="button" class="btn btn-success" @click="addProductToComposition(p)">Добавить</button>
                </td>
              </tr>
            </tbody>
          </v-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {dateNow} from '@/helpers.js'
import {mapGetters, mapActions} from 'vuex'
import vTable from '@/components/v-table'

export default {
  name: 'CreateOrder',
  components: { vTable },
  data: () => ({
    selectProducts: [],
    form: {
      name: null,
      price: null,
      amount: 1
    }
  }),
  computed: {
    ...mapGetters(['storage']),
    orderCost() {
      return this.selectProducts.reduce((sum, p) => sum += p.cost, 0).toFixed(2)
    }
  },
  methods: {
    ...mapActions(['createOrderItem']),
    addProductToComposition(product) {
      const selectValue = +prompt('Кол-во')
      const p = {...product}
      p.value = selectValue
      p.cost = (product.cost / (product.value * 1000) * selectValue) * 1000
      this.selectProducts.push(p)
    },
    createOrder() {
      const order = {
        id: Date.now(),
        date: dateNow(),
        name: this.form.name,
        price: +this.form.price,
        cost: this.orderCost,
        amount: this.form.amount,
        products: this.selectProducts
      }
      this.createOrderItem(order)

      this.form = {
        name: null,
        price: null,
        amount: 1
      }
      this.selectProducts = []
    },
    deleteCompositionItem(id) {
      this.selectProducts = this.selectProducts.filter(p => p.id !== id)
    }
  }
}
</script>

<style lang="scss" scoped>
  .create-order {
    padding-top: 2rem;
    &__form {
      .btn {
        margin-top: 1rem;
      }
      .badge {
        padding: .5rem .7rem;
        color: #fff;
        margin-right: .5rem;
        margin-bottom: .5rem;
        cursor: pointer;
      }
    }
    &__composition {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 1rem;
    }
  }
</style>