<template>
  <div class="storage">
    <div class="storage-top">
      <button 
        type="button" 
        class="btn btn-success" 
        @click="modalVisible = true"
      >Добавить
      </button>
    </div>
    <v-table>
      <thead>
        <tr>
          <th scope="col">Дата</th>
          <th scope="col">Название</th>
          <th scope="col">Кол-во</th>
          <th scope="col">Цена закупки</th>
          <th scope="col">Цена продажи</th>
          <th scope="col"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="{id, date, name, unit, value, cost, price} in storage" :key=id>
          <td>{{ date }}</td>
          <td>{{ name }}</td>
          <td>{{ value | unit(unit) }}</td>
          <td>{{ cost }} руб.</td>
          <td>{{ price }} руб.</td>
          <td>
            <button
              type="button" 
              class="btn btn-sm btn-danger"
              @click="deleteStorageItem(id)"
            >
              Удалить
            </button>
          </td>
        </tr>
      </tbody>
    </v-table>
    
    <v-modal v-if="modalVisible" label="Добавить на склад" @close="closeModal">
      <form>
        <div class="form-group">
          <label for="nameProduct">Название продукта:</label>
          <input type="text" class="form-control" id="nameProduct" v-model="form.name" required>
        </div>

        <div class="form-group">
          <label for="unitProduct">Ед. измерения:</label>
          <select class="form-control" id="unitProduct" v-model="form.unit">
            <option value="kg">кг.</option>
            <option value="gr">г.</option>
            <option value="thing">шт.</option>
          </select>
        </div>

        <div class="form-group">
          <label for="valueProduct">Количество:</label>
          <input type="number" class="form-control" id="valueProduct" v-model="form.value" required>
        </div>

        <div class="form-group">
          <label for="costProduct">Цена закупки:</label>
          <input type="number" class="form-control" id="costProduct" v-model="form.cost" required>
        </div>

        <div class="form-group">
          <label for="priceProduct">Цена продажи:</label>
          <input type="number" class="form-control" id="priceProduct" v-model="form.price" required>
        </div>

      </form>
    
      <template v-slot:footer>
        <button type="button" class="btn btn-primary" @click="addProduct">Добавить</button>
        <button 
          type="button" 
          class="btn btn-secondary" 
          data-dismiss="modal" 
          @click="modalVisible = false"
        >Выйти
        </button>
      </template>
    </v-modal>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import {dateNow} from '@/helpers.js'
import vTable from '@/components/v-table'
import vModal from '@/components/v-modal'

export default {
  name: 'Storage',
  components: {
    vTable,
    vModal
  },
  data: () => ({
    modalVisible: false,
    form: {
      name: null,
      unit: null,
      value: null,
      cost: null,
      price: null
    }
  }),
  computed: {
    ...mapGetters(['storage'])
  },
  methods: {
    ...mapActions(['deleteStorageItem', 'addProductToStorage']),
    closeModal() {
      this.modalVisible = false
    },
    addProduct() {
      const product = {
        id: Date.now(),
        date: dateNow(),
        name: this.form.name,
        unit: this.form.unit,
        value: this.form.value,
        cost: this.form.cost,
        price: this.form.price
      }
      this.addProductToStorage(product)

      this.clearForm()
      this.modalVisible = false
    },
    clearForm() {
      this.form.name = null
      this.form.unit = null
      this.form.value = null
      this.form.cost = null
      this.form.price = null
    }
  }
}
</script>

<style lang="scss" scoped>
  .storage {
    &-top {
      display: flex;
      justify-content: flex-end;
      padding: 1rem 0;   
    }
  }
</style>