<template>
  <div class="orders">
    <div class="orders-top">
      <div class="orders-sum"><b>Cумма:</b> {{sumTotal}} руб.</div>
      <div class="orders-filter">
        <label for="start">
          От:
          <input type="date" id="start" v-model="dateStart">
        </label>
        <label for="end">
          До:
          <input type="date" id="end" v-model="dateEnd">
        </label>
      </div>
    </div>
    
    <v-table>
      <thead>
        <tr>
          <th scope="col">Дата</th>
          <th scope="col">Название</th>
          <th scope="col">Состав</th>
          <th scope="col">Кол-во</th>
          <th scope="col">Себестоимость(шт)</th>
          <th scope="col">Цена продажи(шт)</th>
          <th scope="col">Общая сумма</th>
          <th scope="col"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="{id, date, name, products, cost, price, amount} in filterOrders" :key=id>
          <td>{{ date }}</td>
          <td>{{ name }}</td>
          <td>{{ products | composition }}</td>
          <td>{{ amount }}</td>
          <td>{{ cost }} руб.</td>
          <td>{{ price }} руб.</td>
          <td>{{ price * amount }} руб.</td>
          <td>
            <button
              type="button" 
              class="btn btn-sm btn-danger"
              @click="deleteOrder(id)"
            >
              Удалить
            </button>
          </td>
        </tr>
      </tbody>
    </v-table>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import vTable from '@/components/v-table'

export default {
  name: 'Orders',
  components: {
    vTable
  },
  data: () => ({
    dateStart: null,
    dateEnd: null
  }),
  computed: {
    ...mapGetters(['orders']),
    filterOrders() {
      if (this.dateStart && this.dateEnd) {
        const startDate = new Date(this.dateStart)
        const endDate = new Date(this.dateEnd)

        return this.orders.filter(o => new Date(o.date) >= startDate && new Date(o.date) <= endDate)
      }
      return this.orders
    },
    sumTotal() {
      return this.filterOrders.reduce((total, o) => total += o.price * o.amount, 0)
    }
  },
  methods: {
    ...mapActions(['deleteOrder']),
    calculateAmount(products) {
      console.log(products)
    }
  },
  filters: {
    composition(value) {
      return value.map(p => p.name.toLowerCase()).join(', ')
    }
  }
}
</script>

