import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    storage: [
      { id: 1, date: '2021.04.14', name: 'Огурец', unit: 'kg', value: 70, cost: 5000, price: 6000 },
      { id: 2, date: '2021.01.16', name: 'Краб', unit: 'kg', value: 20, cost: 30000, price: 35000 },
      { id: 3, date: '2020.05.22', name: 'Хаси', unit: 'thing', value: 100, cost: 500, price: 1000 },
      { id: 4, date: '2020.05.22', name: 'Рис', unit: 'kg', value: 150, cost: 20000, price: 28000 },
      { id: 5, date: '2020.05.22', name: 'Соль', unit: 'gr', value: 800, cost: 100, price: 150 },
    ],
    orders: [
      {
        id: 1, date: '2021.04.14', name: 'Филадельфия с крабом', cost: 1000, price: 1500, amount: 2,
        products: [
          {
            id: 1,
            name: "Огурец",
            unit: 'kg',
            value: 0.3,
            cost: 375,
            price: 400,
          },
          {
            id: 2,
            name: "Краб",
            unit: 'kg',
            value: 0.5,
            cost: 1000,
            price: 1200,
          },
        ]
      },
      {
        id: 2, date: '2021.01.16', name: 'Огуречный ролл', cost: 1200, price: 1600, amount: 3,
        products: [
          {
            id: 1,
            name: "Огурец",
            unit: 'kg',
            value: 0.3,
            cost: 375,
            price: 400,
          },
        ]
      }
    ]
  },
  mutations: {
    DELETE_STORAGE_ITEM(state, id) {
      state.storage = state.storage.filter(p => p.id !== id)
    },
    DELETE_ORDER(state, id) {
      state.orders = state.orders.filter(o => o.id !== id)
    },
    ADD_PRODUCT_TO_STORAGE(state, product) {
      state.storage = [product, ...state.storage]
    },
    CREATE_ORDER_ITEM(state, order) {
      state.orders = [order, ...state.orders]

      for(let i = 0; i < order.products.length; i++) {
        const idx = state.storage.findIndex(p => p.id === order.products[i].id)
        state.storage[idx].value = state.storage[idx].value - (order.products[i].value * order.amount)
      }

      
    }
  },
  actions: {
    deleteStorageItem({commit}, id) {
      // API запрос на удаление
      commit('DELETE_STORAGE_ITEM', id)
    },
    deleteOrder({commit}, id) {
      // API запрос на удаление
      commit('DELETE_ORDER', id)
    },
    addProductToStorage({commit}, product) {
      commit('ADD_PRODUCT_TO_STORAGE', product)
    },
    createOrderItem({commit}, order) {
      commit('CREATE_ORDER_ITEM', order)
    }
  },
  getters: {
    storage(state) {
      return state.storage
    },
    orders(state) {
      return state.orders
    }
  }
})
